import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Icon, Button } from 'react-materialize';
import './RecipeItem.css';
import { favoriteRecipe } from '../actions';

class RecipeItem extends Component {
  constructor() {
    super();

    this.state = {
      favoriteSelected: false
    };
  }
  favoriteRecipeSelect(recipe) {
    this.props.favoriteRecipe(recipe);
    this.setState({ favoriteSelected: true });
  }
  render() {
    return (
      <div className="center-align">
        <Row className="recipe-row valign-wrapper">
          <Col>
            {this.state.favoriteSelected ? (
              <Button className="btn-floating">
                <Icon small>star</Icon>
              </Button>
            ) : (
              <Button
                className="btn-floating"
                onClick={() => this.favoriteRecipeSelect(this.props.recipe)}
              >
                <Icon small>star_border</Icon>
              </Button>
            )}
          </Col>
          <Col s={6} m={9} l={9}>
            <div>
              <a
                className="black-text"
                href={this.props.recipe.href}
                target="_blank"
              >
                {this.props.recipe.title}
              </a>
            </div>
          </Col>
          <Col s={6} m={3} l={3}>
            <div>
              <img
                className="right-align"
                src={this.props.recipe.thumbnail}
                alt="No thumbnail"
              />
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}

export default connect(null, { favoriteRecipe })(RecipeItem);



