import React, { Component } from 'react';
import SearchRecipes from './SearchRecipes';
import RecipeList from './RecipeList';

class App extends Component {
  render() {
    return (
      <div className="container">
        <h2>Recipe finder</h2>
        <SearchRecipes />
        <RecipeList className="center" />
      </div>
    );
  }
}

export default App;



